"use server";

import fs from "fs";
import path from "path";
import { v4 as uuidv4 } from "uuid";

const PAYMENTS_DB_PATH = path.join(process.cwd(), "data", "payments.json");

// Initialize payments DB
if (!fs.existsSync(PAYMENTS_DB_PATH)) {
    fs.writeFileSync(PAYMENTS_DB_PATH, JSON.stringify([], null, 2));
}

interface Payment {
    id: string;
    bookingId: string;
    studioName: string;
    userName: string;
    userEmail: string;
    amount: number;
    status: "paid" | "pending" | "failed";
    paymentMethod: "stripe" | "credit" | "other";
    date: string;
    dueDate?: string;
    createdAt: string;
}

// Get all payments
export async function fetchPayments(): Promise<Payment[]> {
    try {
        const data = fs.readFileSync(PAYMENTS_DB_PATH, "utf-8");
        return JSON.parse(data);
    } catch (error) {
        return [];
    }
}

