// Create a new payment record (called when booking is created)
export async function createPayment(data: {
    bookingId: string;
    studioName: string;
    userName: string;
    userEmail: string;
    amount: number;
    paymentMethod: "stripe" | "credit" | "other";
}): Promise<{ success: boolean; paymentId: string }> {
    const payments = await fetchPayments();

    const newPayment: Payment = {
        id: uuidv4(),
        ...data,
        status: "pending",
        date: new Date().toISOString().split("T")[0],
        dueDate: new Date(Date.now() + 7 * 24 * 60 * 60 * 1000)
            .toISOString()
            .split("T")[0], // 7 days from now
        createdAt: new Date().toISOString(),
    };

    payments.push(newPayment);
    fs.writeFileSync(PAYMENTS_DB_PATH, JSON.stringify(payments, null, 2));

    return { success: true, paymentId: newPayment.id };
}

// Update payment status (e.g., after Stripe webhook)
export async function updatePaymentStatus(
    paymentId: string,
    status: "paid" | "pending" | "failed"
): Promise<{ success: boolean }> {
    const payments = await fetchPayments();
    const paymentIndex = payments.findIndex((p) => p.id === paymentId);

    if (paymentIndex === -1) {
        return { success: false };
    }

    payments[paymentIndex].status = status;
    fs.writeFileSync(PAYMENTS_DB_PATH, JSON.stringify(payments, null, 2));

    return { success: true };
}

